/* Estilos generales */
body {
  font-family: Verdana, sans-serif;
  background: url("./assets/images/biblioteca-fondo.jpg") no-repeat center center;
  background-size: cover;
  margin: 0;
  padding: 0;
}

/* Contenedor principal del formulario */
.login-container {
  width: 400px;
  height: 500px;
  margin: 0 auto;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 50px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  margin-top: 50px;
  text-align: center;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
}

/* Formulario de login */
.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Logo de la escuela */
.school-logo {
  width: 100px;
  margin-bottom: 20px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

/* Agrupación de los campos del formulario */
.form-group {
  position: relative;
  width: 75%;
  margin-bottom: 15px;
}

/* Estilo de los íconos de usuario y contraseña */
.icon {
  position: absolute;
  left: 20px;
  top: 65%;
  transform: translateY(-50%);
  color: #333;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
}

/* Estilo para los campos de entrada */
input {
  width: 80%;
  padding: 10px 10px 10px 35px;  /* Espacio para los íconos */
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

/* Estilo del botón de inicio de sesión */
.login-btn {
  background-color: #00aaff;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  width: 40%;
  font-family: Verdana, sans-serif;
}

/* Efecto hover en el botón de inicio de sesión */
.login-btn:hover {
  background-color: #033f5e;
}

/* Mensaje de error */
.error-message {
  color: red;
  font-size: 14px;
}

/* Ajuste para el CAPTCHA */
.recaptcha-container {
  margin-top: 15px;
  margin-bottom: 15px;
}

